<template>
  <div class="ml-characteristic-table">
    <div
      class="ml-characteristic-table__row"
      :class="{
        'ml-characteristic-table__row--section': withSpacer,
      }"
      v-if="showEmpty || health"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.HEALTH }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': healthUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ health }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || elementalDamage"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.ELEMENTAL_DAMAGE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': elementalDamageUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ elementalDamage }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || bloodDamage"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.BLOOD_DAMAGE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': bloodDamageUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ bloodDamage }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || chaosDamage"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.CHAOS_DAMAGE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': chaosDamageUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ chaosDamage }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      :class="{
        'ml-characteristic-table__row--section': withSpacer,
      }"
      v-if="showEmpty || mentalDamage"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.MENTAL_DAMAGE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': mentalDamageUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ mentalDamage }}
      </p>
    </div>

    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || elementalResistance"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.ELEMENTAL_RESISTANCE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': elementalResistanceUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ elementalResistance }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || bloodResistance"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.BLOOD_RESISTANCE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': bloodResistanceUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ bloodResistance }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || chaosResistance"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.CHAOS_RESISTANCE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': chaosResistanceUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ chaosResistance }}
      </p>
    </div>
    <div
      class="ml-characteristic-table__row"
      v-if="showEmpty || mentalResistance"
    >
      <p
        class="ml-characteristic-table__column"
        :class="{
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ AVATAR_SETTING_NAME_MAP.MENTAL_RESISTANCE }}:
      </p>
      <p
        class="ml-characteristic-table__column ml-bold"
        :class="{
          'ml-color-blue': mentalResistanceUpdated,
          'ml-paragraph-mobile': mobileParagraph,
        }"
      >
        {{ mentalResistance }}
      </p>
    </div>

  </div>
</template>

<script>
import {
  AVATAR_SETTING_MAP,
  AVATAR_SETTING_NAME_MAP,
} from '@script/constant/avatar-setting';

export default {
  name: 'Ml-characteristic-table',
  props: {
    highlightUpdates: {
      type: Boolean,
      default: false,
    },
    mobileParagraph: {
      type: Boolean,
      default: false,
    },
    withSpacer: {
      type: Boolean,
      default: false,
    },
    showEmpty: {
      type: Boolean,
      default: false,
    },
    characteristicMap: {
      type: Object,
      default: () => ({}),
    },
    characteristicUpdateListMap: {
      type: Object,
      default: () => ({}),
    },
  },
  data: () => ({
    AVATAR_SETTING_NAME_MAP,
  }),
  computed: {
    health() {
      return this.characteristicMap[AVATAR_SETTING_MAP.HEALTH] || 0;
    },
    healthUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.HEALTH];
    },
    elementalDamage() {
      return this.characteristicMap[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE] || 0;
    },
    elementalDamageUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE];
    },
    bloodDamage() {
      return this.characteristicMap[AVATAR_SETTING_MAP.BLOOD_DAMAGE] || 0;
    },
    bloodDamageUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.BLOOD_DAMAGE];
    },
    chaosDamage() {
      return this.characteristicMap[AVATAR_SETTING_MAP.CHAOS_DAMAGE] || 0;
    },
    chaosDamageUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.CHAOS_DAMAGE];
    },
    mentalDamage() {
      return this.characteristicMap[AVATAR_SETTING_MAP.MENTAL_DAMAGE] || 0;
    },
    mentalDamageUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.MENTAL_DAMAGE];
    },
    elementalResistance() {
      return this.characteristicMap[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE] || 0;
    },
    elementalResistanceUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE];
    },
    bloodResistance() {
      return this.characteristicMap[AVATAR_SETTING_MAP.BLOOD_RESISTANCE] || 0;
    },
    bloodResistanceUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.BLOOD_RESISTANCE];
    },
    chaosResistance() {
      return this.characteristicMap[AVATAR_SETTING_MAP.CHAOS_RESISTANCE] || 0;
    },
    chaosResistanceUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.CHAOS_RESISTANCE];
    },
    mentalResistance() {
      return this.characteristicMap[AVATAR_SETTING_MAP.MENTAL_RESISTANCE] || 0;
    },
    mentalResistanceUpdated() {
      return this.highlightUpdates && this.characteristicUpdateListMap[AVATAR_SETTING_MAP.MENTAL_RESISTANCE];
    },
  },
};
</script>

<style lang="scss">
  @import '@style/typography.scss';
  @import './characteristic-table.scss';
</style>
