export const  equipmentLocalStorageNormalizer = (item, itemList) => {
  const itemId = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.ITEM_ID]];
  if(itemId === null) {
    return equipmentEmpty();
  }

  const equipment = JSON.parse(JSON.stringify(itemList[itemId]));

  const itemHealth = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.HEALTH]];
  const itemElementalDamage = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE]];
  const itemBloodDamage = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.BLOOD_DAMAGE]];
  const itemChaosDamage = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.CHAOS_DAMAGE]];
  const itemMentalDamage = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.MENTAL_DAMAGE]];
  const itemElementalResistance = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE]];
  const itemBloodResistance = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.BLOOD_RESISTANCE]];
  const itemChaosResistance = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.CHAOS_RESISTANCE]];
  const itemMentalResistance = item[ATTRIBUTES_KEY_LIST[AVATAR_SETTING_MAP.MENTAL_RESISTANCE]];


  // health
  if(itemHealth > equipment[AVATAR_SETTING_MAP.HEALTH].base) {
    equipment[AVATAR_SETTING_MAP.HEALTH].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.HEALTH].value = itemHealth;

  // elemental damage
  if(itemElementalDamage > equipment[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE].base) {
    equipment[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE].value = itemElementalDamage;

  // blood damage
  if(itemBloodDamage > equipment[AVATAR_SETTING_MAP.BLOOD_DAMAGE].base) {
    equipment[AVATAR_SETTING_MAP.BLOOD_DAMAGE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.BLOOD_DAMAGE].value = itemBloodDamage;

  // chaos damage
  if(itemChaosDamage > equipment[AVATAR_SETTING_MAP.CHAOS_DAMAGE].base) {
    equipment[AVATAR_SETTING_MAP.CHAOS_DAMAGE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.CHAOS_DAMAGE].value = itemChaosDamage;

  // mental damage
  if(itemMentalDamage > equipment[AVATAR_SETTING_MAP.MENTAL_DAMAGE].base) {
    equipment[AVATAR_SETTING_MAP.MENTAL_DAMAGE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.MENTAL_DAMAGE].value = itemMentalDamage;

  // elemental resistance
  if(itemElementalResistance > equipment[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE].base) {
    equipment[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE].value = itemElementalResistance;

  // blood resistance
  if(itemBloodResistance > equipment[AVATAR_SETTING_MAP.BLOOD_RESISTANCE].base) {
    equipment[AVATAR_SETTING_MAP.BLOOD_RESISTANCE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.BLOOD_RESISTANCE].value = itemBloodResistance;

  // chaos resistance
  if(itemChaosResistance > equipment[AVATAR_SETTING_MAP.CHAOS_RESISTANCE].base) {
    equipment[AVATAR_SETTING_MAP.CHAOS_RESISTANCE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.CHAOS_RESISTANCE].value = itemChaosResistance;

  // mental resistance
  if(itemMentalResistance > equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].base) {
    equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].updated = true;
  }
  equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].value = itemMentalResistance;
  
  return equipment;
};

export const emptyItemPropMap = () => {
  return {
    itemType: '',
    key: '',
  };
};
