<template>
  <main class="ml-wardrobe">
    
    <div class="ml-wardrobe__header">
      <div class="container">
        <div class="ml-wardrobe__header-content">
          <h2 class="ml-wardrobe__header-intro ml-color-white ml-regular">
             
            <a
              href="https://www.magiclands.ru/index.jqn"
              target="_blank"
              class="ml-link ml-color-white"
              rel="noreferrer noopenner"
            >
               
            </a>
          </h2>
        </div>
      </div>
    </div>
    
    <div class="ml-wardrobe__body">
      <div class="container">

        <div class="ml-wardrobe__body-content">
          <!-- start character view -->
          <div class="ml-wardrobe__character">
            <ml-charecter />
          </div>
          <!-- end character view -->

          <!-- start indicators -->
          <div class="ml-wardrobe__characteristic-indicator">
            <ml-character-indicator />
          </div>
          <!-- end indicators -->
        </div>
      </div>
    </div>

    <div class="ml-wardrobe__action-menu">
      <div class="container">
        <div class="ml-wardrobe__action-menu-content">
          <button
            @click="resetComplect"
            class="ml-wardrobe__action-menu-button ml-button ml-button--action"
          >
             
          </button>
          <button
            @click="saveComplect"
            class="ml-wardrobe__action-menu-button ml-button ml-button--action"
          >
               
            <p class="ml-wardrobe__action-menu-button-helper ml-paragraph-mobile">
                   
            </p>
          </button>
          <button
            v-if="hasLocalSlorageSet"
            @click="deleteComplect"
            class="ml-wardrobe__action-menu-button ml-button ml-button--action"
          >
              
          </button>
        </div>
      </div>
    </div>

    <div class="ml-wardrobe__footer">
      <div class="container">
        <div class="ml-wardrobe__footer-container">
          <span class="ml-wardrobe__footer-by">
            by swarty
          </span>
          <span class="ml-wardrobe__version">
            v 1.1.0
          </span>
        </div>
      </div>
    </div>

    <ml-equipment-catalog class="ml-wardrobe__equipment-modal" />
    <ml-edit-equipment-modal class="ml-wardrobe__edit-equipment-modal" />

  </main>

</template>

<script>
import MlCharecter from '@component/character/character';
import MlCharacterIndicator from '@component/character-indicator/character-indicator';
import MlEquipmentCatalog from '@component/equipment-catalog/equipment-catalog';
import MlEditEquipmentModal from '@component/edit-equipment-modal/edit-equipment-modal';

import { localeStoreItemUglify } from '@script/locale-store-item';
import { CLOTHES_KEY_LIST } from '@script/constant/key-list';
import { CLOTHES_TYPE } from '@script/constant/clothes-type';
import { equipmentLocalStorageNormalizer } from '@script/equipment-normalizer';

const LOCAL_STORAGE_KEY = 'ml-wardrobe-set';
const WINDOW = window;

export default {
  name: 'Ml-wordrobe',
  components: {
    MlCharecter,
    MlCharacterIndicator,
    MlEquipmentCatalog,
    MlEditEquipmentModal,
  },
  data: () => ({
    hasLocalSlorageSet: false,
  }),
  created() {
    this.hasLocalSlorageSet = !!WINDOW.localStorage.getItem(LOCAL_STORAGE_KEY);
    this.$store.dispatch('loadData', this.loadComplect);
  },
  methods: {
    resetComplect() {
      this.$store.dispatch('resetComplect');
    },
    saveComplect() {
      const preparedObject = {
        level: this.$store.state.avatarLevel,
      };

      const amuletList = Object.values(this.$store.state.equipped[CLOTHES_TYPE.AMULET]);
      amuletList.forEach((item, index) => {
        preparedObject[CLOTHES_KEY_LIST[`AMULET_${index}`]] = localeStoreItemUglify(item);
      });

      const ringList = Object.values(this.$store.state.equipped[CLOTHES_TYPE.RING]);
      ringList.forEach((item, index) => {
        preparedObject[CLOTHES_KEY_LIST[`RING_${index}`]] = localeStoreItemUglify(item);
      });
      
      preparedObject[CLOTHES_KEY_LIST.WEAPON_LEFT] = localeStoreItemUglify(this.$store.state.equipped[CLOTHES_TYPE.WEAPON].left);
      preparedObject[CLOTHES_KEY_LIST.WEAPON_RIGHT] = localeStoreItemUglify(this.$store.state.equipped[CLOTHES_TYPE.WEAPON].right);
      preparedObject[CLOTHES_KEY_LIST.DRESS] = localeStoreItemUglify(this.$store.state.equipped[CLOTHES_TYPE.DRESS]);
      preparedObject[CLOTHES_KEY_LIST.HAT] = localeStoreItemUglify(this.$store.state.equipped[CLOTHES_TYPE.HAT]);
      preparedObject[CLOTHES_KEY_LIST.SHOE] = localeStoreItemUglify(this.$store.state.equipped[CLOTHES_TYPE.SHOE]);
      preparedObject[CLOTHES_KEY_LIST.PANT] = localeStoreItemUglify(this.$store.state.equipped[CLOTHES_TYPE.PANT]);

      WINDOW.localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(preparedObject));
      this.hasLocalSlorageSet = true;
    },
    deleteComplect() {
      WINDOW.localStorage.removeItem(LOCAL_STORAGE_KEY);
      this.hasLocalSlorageSet = false;
    },
    loadComplect() {
      const localStorage = WINDOW.localStorage.getItem(LOCAL_STORAGE_KEY);
      if(!localStorage) return;
      const data = JSON.parse(WINDOW.localStorage.getItem(LOCAL_STORAGE_KEY));

      this.$store.dispatch('setLevel', data.level);

      this.$store.state.equipped[CLOTHES_TYPE.AMULET][0] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.AMULET_0], this.$store.state.amuletList);
      this.$store.state.equipped[CLOTHES_TYPE.AMULET][1] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.AMULET_1], this.$store.state.amuletList);
      this.$store.state.equipped[CLOTHES_TYPE.AMULET][2] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.AMULET_2], this.$store.state.amuletList);
      this.$store.state.equipped[CLOTHES_TYPE.AMULET][3] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.AMULET_3], this.$store.state.amuletList);

      this.$store.state.equipped[CLOTHES_TYPE.RING][0] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.RING_0], this.$store.state.ringList);
      this.$store.state.equipped[CLOTHES_TYPE.RING][1] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.RING_1], this.$store.state.ringList);
      this.$store.state.equipped[CLOTHES_TYPE.RING][2] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.RING_2], this.$store.state.ringList);
      this.$store.state.equipped[CLOTHES_TYPE.RING][3] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.RING_3], this.$store.state.ringList);

      this.$store.state.equipped[CLOTHES_TYPE.WEAPON].left = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.WEAPON_LEFT], this.$store.state.weaponList);
      this.$store.state.equipped[CLOTHES_TYPE.WEAPON].right = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.WEAPON_RIGHT], this.$store.state.weaponList);

      this.$store.state.equipped[CLOTHES_TYPE.DRESS] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.DRESS], this.$store.state.dressList);
      this.$store.state.equipped[CLOTHES_TYPE.HAT] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.HAT], this.$store.state.hatList);
      this.$store.state.equipped[CLOTHES_TYPE.SHOE] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.SHOE], this.$store.state.shoeList);
      this.$store.state.equipped[CLOTHES_TYPE.PANT] = equipmentLocalStorageNormalizer(data[CLOTHES_KEY_LIST.PANT], this.$store.state.pantList);
    },
  },
};
</script>

<style lang="scss">
  @import '@style/reset.scss';
  @import '@style/normalize.scss';
  @import '@style/general.scss';
  @import '@style/font-family.scss';
  @import './wardrope.scss';
</style>
