import { createStore } from 'vuex';
import {
  getHatList,
  getAmuletList,
  getShoeList,
  getDressList,
  getPantList,
  getRingList,
  getWeaponList,
} from '@script/api/api';
import { CLOTHES_TYPE } from '@script/constant/clothes-type';
import {
  equipmentNormalizer,
  equipmentEmpty,
  emptyItemPropMap,
} from '@script/equipment-normalizer';
import { generalApiError } from '@script/general-api-error';
import { DEFAULT_LEVEL } from '@script/constant/avatar-setting';

export default createStore({
  state: {
    hatList: [],
    amuletList: [],
    shoeList: [],
    dressList: [],
    pantList: [],
    ringList: [],
    weaponList: [],

    activeEquipmentProp: emptyItemPropMap(),
    editEquipmentModalVisible: false,
    equipmentCatalogVisible: false,

    avatarLevel: DEFAULT_LEVEL,
    equipped: {
      [CLOTHES_TYPE.AMULET]: {
        0: equipmentEmpty(),
        1: equipmentEmpty(),
        2: equipmentEmpty(),
        3: equipmentEmpty(),
      },
      [CLOTHES_TYPE.RING]: {
        0: equipmentEmpty(),
        1: equipmentEmpty(),
        2: equipmentEmpty(),
        3: equipmentEmpty(),
      },
      [CLOTHES_TYPE.WEAPON]: {
        left: equipmentEmpty(),
        right: equipmentEmpty(),
      },
      [CLOTHES_TYPE.HAT]: equipmentEmpty(),
      [CLOTHES_TYPE.SHOE]: equipmentEmpty(),
      [CLOTHES_TYPE.DRESS]: equipmentEmpty(),
      [CLOTHES_TYPE.PANT]: equipmentEmpty(),
    },
  },
  mutations: {
    loadData(state, callback) {
      const promiseArray = [
        getHatList().then((hatList) => {
          state.hatList = hatList.map((hat, index) => equipmentNormalizer(hat, index));
        }),
        getAmuletList().then((amuletList) => {
          state.amuletList = amuletList.map((amulet, index) => equipmentNormalizer(amulet, index));
        }),
        getShoeList().then((shoeList) => {
          state.shoeList = shoeList.map((shoe, index) => equipmentNormalizer(shoe, index));
        }),
        getDressList().then((dressList) => {
          state.dressList = dressList.map((dress, index) => equipmentNormalizer(dress, index));
        }),
        getPantList().then((pantList) => {
          state.pantList = pantList.map((pant, index) => equipmentNormalizer(pant, index));
        }),
        getRingList().then((ringList) => {
          state.ringList = ringList.map((ring, index) => equipmentNormalizer(ring, index));
        }),
        getWeaponList().then((weaponList) => {
          state.weaponList = weaponList.map((weapon, index) => equipmentNormalizer(weapon, index));
        })
      ];
      
      Promise
        .all(promiseArray)
        .catch(generalApiError)
        .finally(callback)
    },
    setActiveEquipmentProp(state, itemProp = emptyItemPropMap()) {
      state.activeEquipmentProp = itemProp;
    },
    setLevel(state, level) {
      state.avatarLevel = level;
      
      // if level less then equipped clothes => put off them
      const amuletList = Object.values(state.equipped[CLOTHES_TYPE.AMULET]);
      amuletList.forEach( (amulet, amuletIndex) => {
        if(amulet.itemLevel > state.avatarLevel) {
          state.equipped[CLOTHES_TYPE.AMULET][amuletIndex] = equipmentEmpty();
        }
      });

      const ringList = Object.values(state.equipped[CLOTHES_TYPE.RING]);
      ringList.forEach( (ring, ringIndex) => {
        if(ring.itemLevel > state.avatarLevel) {
          state.equipped[CLOTHES_TYPE.RING][ringIndex] = equipmentEmpty();
        }
      });
      
      const weaponList = Object.values(state.equipped[CLOTHES_TYPE.WEAPON]);
      const WEAPON_KEYS = Object.keys(state.equipped[CLOTHES_TYPE.WEAPON]);
      weaponList.forEach( (weapon, weaponIndex) => {
        if(weapon.itemLevel > state.avatarLevel) {
          state.equipped[CLOTHES_TYPE.WEAPON][WEAPON_KEYS[weaponIndex]] = equipmentEmpty();
        }
      });

      if(state.equipped[CLOTHES_TYPE.HAT].itemLevel > state.avatarLevel) {
        state.equipped[CLOTHES_TYPE.HAT] = equipmentEmpty();
      }
      if(state.equipped[CLOTHES_TYPE.SHOE].itemLevel > state.avatarLevel) {
        state.equipped[CLOTHES_TYPE.SHOE] = equipmentEmpty();
      }
      if(state.equipped[CLOTHES_TYPE.DRESS].itemLevel > state.avatarLevel) {
        state.equipped[CLOTHES_TYPE.DRESS] = equipmentEmpty();
      }
      if(state.equipped[CLOTHES_TYPE.PANT].itemLevel > state.avatarLevel) {
        state.equipped[CLOTHES_TYPE.PANT] = equipmentEmpty();
      }
    },
    toggleEditEquipmentModal(state, flag) {
      state.editEquipmentModalVisible = flag;
    },
    toggleEquipmentCatalog(state, flag) {
      state.equipmentCatalogVisible = flag;
    },
    resetComplect(state) {
      state.avatarLevel = DEFAULT_LEVEL;
      for(let index in state.equipped[CLOTHES_TYPE.AMULET]) {
        state.equipped[CLOTHES_TYPE.AMULET][index] = equipmentEmpty();
      }
      for(let index in state.equipped[CLOTHES_TYPE.RING]) {
        state.equipped[CLOTHES_TYPE.RING][index] = equipmentEmpty();
      }
      for(let index in state.equipped[CLOTHES_TYPE.WEAPON]) {
        state.equipped[CLOTHES_TYPE.WEAPON][index] = equipmentEmpty();
      }
      state.equipped[CLOTHES_TYPE.HAT] = equipmentEmpty();
      state.equipped[CLOTHES_TYPE.SHOE] = equipmentEmpty();
      state.equipped[CLOTHES_TYPE.DRESS] = equipmentEmpty();
      state.equipped[CLOTHES_TYPE.PANT] = equipmentEmpty();
    },
  },
  actions: {
    loadData (context, callback) {
      context.commit('loadData', callback);
    },
    setActiveEquipmentProp (context, itemProp) {
      context.commit('setActiveEquipmentProp', itemProp);
    },
    setLevel (context, level) {
      context.commit('setLevel', level);
    },
    toggleEditEquipmentModal (context, flag) {
      context.commit('toggleEditEquipmentModal', flag);
    },
    toggleEquipmentCatalog (context, flag) {
      context.commit('toggleEquipmentCatalog', flag);
    },
    resetComplect (context) {
      context.commit('resetComplect');
    },
  },
  getters: {
    equipmentList(state) {
      switch(state.activeEquipmentProp.itemType) {
        case CLOTHES_TYPE.AMULET:
          return state.amuletList;
        case CLOTHES_TYPE.RING:
          return state.ringList;
        case CLOTHES_TYPE.WEAPON:
          return state.weaponList;
        case CLOTHES_TYPE.HAT:
          return state.hatList;
        case CLOTHES_TYPE.SHOE:
          return state.shoeList;
        case CLOTHES_TYPE.DRESS:
          return state.dressList;
        case CLOTHES_TYPE.PANT:
          return state.pantList;
        default:
          return [];
      }
    }
  },
});
