<template>
  <aside
    class="ml-equipment-catalog"
    :class="{
      'ml-equipment-catalog--opened': showModal,
    }"
  >
    <div
      class="ml-equipment-catalog__layer"
      @click="closeModal"
    />
    <div class="ml-equipment-catalog__content">
      
      <div class="ml-equipment-catalog__content-header">
        <p
          v-if="equipmentList.length"
          class="ml-equipment-catalog__content-header-title"
        >
           :
        </p>
        <p
          v-else
          class="ml-equipment-catalog__no-clothes"
        >
                
        </p>
        <ml-close-icon
          @click="closeModal"
          class="ml-equipment-catalog__content-header-close"
        />
      </div>
      <ul
        v-if="equipmentList.length"
        class="ml-equipment-catalog__list"
      >
        <li
          class="ml-equipment-catalog__list-item"
          v-for="(thing, index) in equipmentList"
          :key="thing.name + index"
          @click="selectEquipment(thing)"
        >
          <div
            class="ml-equipment-catalog__list-item-picture"
            :class="{
              'ml-equipment-catalog__list-item-picture--small': thing.class === CLOTHES_TYPE.AMULET || thing.class === CLOTHES_TYPE.RING,
            }"
          >
            <img
              class="ml-equipment-catalog__list-item-picture-image"
              :src="image(thing.image)"
              :alt="thing.name"
            >
          </div>
          <div class="ml-equipment-catalog__list-item-info">
            <p class="ml-equipment-catalog__list-item-title">
              {{ thing.name }} [{{ thing.itemLevel }}]
            </p>
            <ml-characteristic-table :characteristic-map="{
              [AVATAR_SETTING_MAP.HEALTH]: thing.health.base,
              [AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE]: thing.elementalDamage.base,
              [AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE]: thing.elementalResistance.base,
              [AVATAR_SETTING_MAP.BLOOD_DAMAGE]: thing.bloodDamage.base,
              [AVATAR_SETTING_MAP.BLOOD_RESISTANCE]: thing.bloodResistance.base,
              [AVATAR_SETTING_MAP.MENTAL_DAMAGE]: thing.mentalDamage.base,
              [AVATAR_SETTING_MAP.MENTAL_RESISTANCE]: thing.mentalResistance.base,
              [AVATAR_SETTING_MAP.CHAOS_DAMAGE]: thing.chaosDamage.base,
              [AVATAR_SETTING_MAP.CHAOS_RESISTANCE]: thing.chaosResistance.base,
            }"/>
          </div>
        </li>
      </ul>
    </div>
  </aside>
</template>

<script>
import MlCharacteristicTable from '@component/characteristic-table/characteristic-table';
import MlCloseIcon from '@component/close-icon/close-icon';

import { AVATAR_SETTING_MAP } from '@script/constant/avatar-setting';
import { CLOTHES_TYPE } from '@script/constant/clothes-type';
import { PUBLIC_PATH } from '@script/constant/public-path';
import { emptyItemPropMap } from '@script/equipment-normalizer';

export default {
  name: 'Ml-equipment-catalog',
  components: {
    MlCharacteristicTable,
    MlCloseIcon,
  },
  data: () => ({
    AVATAR_SETTING_MAP,
    CLOTHES_TYPE,
  }),
  computed: {
    showModal() {
      return !!this.$store.state.equipmentCatalogVisible;
    },
    equipmentList() {
      return this.$store.getters.equipmentList
        .filter( (thing) => this.$store.state.avatarLevel >= thing.itemLevel);
    },
  },
  methods: {
    image(route) {
      return PUBLIC_PATH + route;
    },
    closeModal() {
      this.$store.dispatch('setActiveEquipmentProp', emptyItemPropMap());
      this.$store.dispatch('toggleEquipmentCatalog', false);
    },
    selectEquipment(thing) {
      const thingItem = JSON.parse(JSON.stringify(thing));
      const itemType = this.$store.state.activeEquipmentProp.itemType,
            itemKey = this.$store.state.activeEquipmentProp.key;
      if(itemKey !== '') {
        this.$store.state.equipped[itemType][itemKey] = thingItem;
      } else {
        this.$store.state.equipped[itemType] = thingItem;
      }
      this.closeModal();
    },
  },
};
</script>

<style lang="scss">
  @import './equipment-catalog.scss';
</style>
