<template>
  <div
    class="ml-clothes-slot"
    @mouseenter="onMouseEnter"
    @mouseleave="onMouseLeave"
    @mousemove="onMouseMove"
  >
    <img
      @click="onClothesSlotClick"
      class="ml-clothes-slot__image"
      :src="image"
      :alt="clothesType"
    >
    <div
      v-show="equipment.class"
      class="ml-clothes-slot__navigation"
    >
      <ml-edit-icon
        v-show="showUpdateIcon && CLOTHES_TYPE.AMULET !== clothesType"
        @click="showUpdateModal"
        class="ml-clothes-slot__navigation-button ml-button ml-button--small"
      />
      <ml-close-icon
        v-show="showResetIcon"
        @click="resetEquipment"
        class="ml-clothes-slot__navigation-button ml-button ml-button--small"
      />
    </div>
  </div>
</template>

<script>
import MlCloseIcon from '@component/close-icon/close-icon';
import MlEditIcon from '@component/edit-icon/edit-icon';

import { CLOTHES_TYPE } from '@script/constant/clothes-type';
import { PUBLIC_PATH } from '@script/constant/public-path';
import { equipmentEmpty } from '@script/equipment-normalizer';

const CLOTHES_STUB_IMAGE_MAP = {
  [CLOTHES_TYPE.AMULET]: require('@picture/amulet-placeholder.gif'),
  [CLOTHES_TYPE.RING]: require('@picture/rings-placeholder.gif'),
  [CLOTHES_TYPE.WEAPON]: require('@picture/weapon-placeholder.gif'),
  [CLOTHES_TYPE.HAT]: require('@picture/hat-placeholder.gif'),
  [CLOTHES_TYPE.SHOE]: require('@picture/shoes-placeholder.gif'),
  [CLOTHES_TYPE.DRESS]: require('@picture/dress-placeholder.gif'),
  [CLOTHES_TYPE.PANT]: require('@picture/pants-placeholder.gif'),
};

export default {
  name: 'Ml-clothes-slot',
  components: {
    MlCloseIcon,
    MlEditIcon,
  },
  props: {
    clothesType: {
      type: String,
      required: true,
    },
    clothesKey: {
      type: [String, Number],
      default: '',
    },
  },
  data() {
    const { matches: mobileWindow } = window.matchMedia('(max-width: 1023px)');

    const showResetIcon = mobileWindow
      ? true
      : false;
    const showUpdateIcon = mobileWindow
      ? true
      : false;

    return {
      CLOTHES_TYPE,
      showUpdateIcon,
      showResetIcon,
      mobileWindow,
    }
  },
  computed: {
    equipment() {
      return this.getEquipment();
    },
    image() {
      if(this.equipment.image) {
        return `${PUBLIC_PATH}${this.equipment.image}`;
      }

      return CLOTHES_STUB_IMAGE_MAP[this.clothesType];
    },
  },
  methods: {
    getEquipment() {
      return this.clothesKey || this.clothesKey === 0
        ? this.$store.state.equipped[this.clothesType][this.clothesKey]
        : this.$store.state.equipped[this.clothesType];
    },
    onClothesSlotClick() {
      this.$store.dispatch('setActiveEquipmentProp', {
        itemType: this.clothesType,
        key: this.clothesKey,
      });
      this.$store.dispatch('toggleEquipmentCatalog', true);
    },
    onMouseEnter() {
      if(!this.equipment.class) return;
      if(!this.mobileWindow) {
        this.showUpdateIcon = true;
        this.showResetIcon = true;
      }
      
      this.$emit('on-mouse-enter', this.equipment);
    },
    onMouseLeave() {
      if(!this.mobileWindow) {
        this.showUpdateIcon = false;
        this.showResetIcon = false;
      }
      this.$emit('on-mouse-leave');
    },
    onMouseMove(event) {
      if(!this.equipment.class) return;
      const { pageX, pageY } = event;
      this.$emit('on-mouse-move', {
        top: pageY,
        left: pageX,
      });
    },
    resetEquipment() {
      if(!this.showResetIcon) return;
      this.onMouseLeave();
      if(this.clothesKey || this.clothesKey === 0) {
        this.$store.state.equipped[this.clothesType][this.clothesKey] = equipmentEmpty();
      } else {
        this.$store.state.equipped[this.clothesType] = equipmentEmpty();
      }
    },
    showUpdateModal() {
      this.$store.dispatch('setActiveEquipmentProp', {
        itemType: this.clothesType,
        key: this.clothesKey,
      });
      this.$store.dispatch('toggleEditEquipmentModal', true);
    },
  },
};
</script>

<style lang="scss">
  @import '@component/button/button.scss';
  @import './clothes-slot.scss';
</style>
