import { AVATAR_SETTING_MAP } from '@script/constant/avatar-setting.js';

// KEY,
// HEALTH
// ELEMENTAL_DAMAGE
// BLOOD_DAMAGE
// CHAOS_DAMAGE
// MENTAL_DAMAGE
// ELEMENTAL_RESISTANCE
// BLOOD_RESISTANCE
// CHAOS_RESISTANCE
// MENTAL_RESISTANCE

export const localeStoreItemUglify = (item) => {
  return [
    item[AVATAR_SETTING_MAP.ITEM_ID],
    item[AVATAR_SETTING_MAP.HEALTH].value,

    item[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE].value,
    item[AVATAR_SETTING_MAP.BLOOD_DAMAGE].value,
    item[AVATAR_SETTING_MAP.CHAOS_DAMAGE].value,
    item[AVATAR_SETTING_MAP.MENTAL_DAMAGE].value,

    item[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE].value,
    item[AVATAR_SETTING_MAP.BLOOD_RESISTANCE].value,
    item[AVATAR_SETTING_MAP.CHAOS_RESISTANCE].value,
    item[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].value,
  ];
};


