            <button
              @click="decreaseValue(AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE, equipment[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE])"
              class="ml-edit-equipment-modal__content-form-navigation-button ml-button ml-button--small"
              :disabled="equipment[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE].value === equipment[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE].base"
            >
