            }"
          >
            {{ equipment[AVATAR_SETTING_MAP.BLOOD_RESISTANCE].value }}
          </span>
          <div class="ml-edit-equipment-modal__content-form-navigation">
            <button
              @click="increaseValue(AVATAR_SETTING_MAP.BLOOD_RESISTANCE, equipment[AVATAR_SETTING_MAP.BLOOD_RESISTANCE])"
              class="ml-edit-equipment-modal__content-form-navigation-button ml-button ml-button--small"
            >
