              class="ml-edit-equipment-modal__content-form-navigation-button ml-button ml-button--small"
              :disabled="equipment[AVATAR_SETTING_MAP.CHAOS_RESISTANCE].value === equipment[AVATAR_SETTING_MAP.CHAOS_RESISTANCE].base"
