          <span class="ml-edit-equipment-modal__content-form-title">
            {{ AVATAR_SETTING_NAME_MAP.MENTAL_RESISTANCE }}
          </span>
          <span
            class="ml-edit-equipment-modal__content-form-value"
            :class="{
              'ml-edit-equipment-modal__content-form-value--updated': equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].updated, 
            }"
          >
            {{ equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].value }}
          </span>
          <div class="ml-edit-equipment-modal__content-form-navigation">
            <button
              @click="increaseValue(AVATAR_SETTING_MAP.MENTAL_RESISTANCE, equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE])"
              class="ml-edit-equipment-modal__content-form-navigation-button ml-button ml-button--small"
            >
              +
            </button>
            <button
              @click="decreaseValue(AVATAR_SETTING_MAP.MENTAL_RESISTANCE, equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE])"
              class="ml-edit-equipment-modal__content-form-navigation-button ml-button ml-button--small"
              :disabled="equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].value === equipment[AVATAR_SETTING_MAP.MENTAL_RESISTANCE].base"
            >
