        <!-- end mental damage -->

      </div>
      
    </div>
  </div>
</template>

<script>
import MlCloseIcon from '@component/close-icon/close-icon';

import {
  AVATAR_SETTING_MAP,
  AVATAR_SETTING_NAME_MAP,
} from '@script/constant/avatar-setting';
import {
  equipmentEmpty,
  emptyItemPropMap,
} from '@script/equipment-normalizer';

const PROPERTY_MULTIPLIER_LIST = {
  [AVATAR_SETTING_MAP.HEALTH]: 5,
  [AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE]: 1,
  [AVATAR_SETTING_MAP.BLOOD_DAMAGE]: 1,
  [AVATAR_SETTING_MAP.CHAOS_DAMAGE]: 1,
  [AVATAR_SETTING_MAP.MENTAL_DAMAGE]: 1,
  [AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE]: 1,
  [AVATAR_SETTING_MAP.BLOOD_RESISTANCE]: 1,
  [AVATAR_SETTING_MAP.CHAOS_RESISTANCE]: 1,
  [AVATAR_SETTING_MAP.MENTAL_RESISTANCE]: 1,
}

export default {
  name: 'Ml-edit-equipment-modal',
  components: {
    MlCloseIcon,
  },
  data: () => ({
    AVATAR_SETTING_MAP,
    AVATAR_SETTING_NAME_MAP,
    equipment: equipmentEmpty(),
  }),
  computed: {
    showModal() {
      return this.$store.state.editEquipmentModalVisible;
    },
    activeEquipmentType() {
      return this.$store.state.activeEquipmentProp.itemType;
    },
    activeEquipmentKey() {
      return this.$store.state.activeEquipmentProp.key;
    },
  },
  methods: {
    closeModal() {
      this.$store.dispatch('setActiveEquipmentProp', emptyItemPropMap());
      this.$store.dispatch('toggleEditEquipmentModal', false);
    },
    setEquipment() {
      if(this.activeEquipmentKey || this.activeEquipmentKey === 0) {
        return this.$store.state.equipped[this.activeEquipmentType][this.activeEquipmentKey];
      } else {
        return this.$store.state.equipped[this.activeEquipmentType];
      }
    },
    increaseValue(type, property) {
      switch(type) {
        case AVATAR_SETTING_MAP.HEALTH:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.HEALTH];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.BLOOD_DAMAGE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.BLOOD_DAMAGE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.CHAOS_DAMAGE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.CHAOS_DAMAGE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.MENTAL_DAMAGE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.MENTAL_DAMAGE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.BLOOD_RESISTANCE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.BLOOD_RESISTANCE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.CHAOS_RESISTANCE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.CHAOS_RESISTANCE];
          property.updated = true;
          break;
        case AVATAR_SETTING_MAP.MENTAL_RESISTANCE:
          property.value += PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.MENTAL_RESISTANCE];
          property.updated = true;
          break;
      }
    },
    decreaseValue(type, property) {
      switch(type) {
        case AVATAR_SETTING_MAP.HEALTH:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.HEALTH] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.HEALTH];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.BLOOD_DAMAGE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.BLOOD_DAMAGE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.BLOOD_DAMAGE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.CHAOS_DAMAGE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.CHAOS_DAMAGE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.CHAOS_DAMAGE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.MENTAL_DAMAGE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.MENTAL_DAMAGE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.MENTAL_DAMAGE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.BLOOD_RESISTANCE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.BLOOD_RESISTANCE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.BLOOD_RESISTANCE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.CHAOS_RESISTANCE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.CHAOS_RESISTANCE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.CHAOS_RESISTANCE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
        case AVATAR_SETTING_MAP.MENTAL_RESISTANCE:
          if(property.value - PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.MENTAL_RESISTANCE] >= property.base) {
            property.value -= PROPERTY_MULTIPLIER_LIST[AVATAR_SETTING_MAP.MENTAL_RESISTANCE];
          }
          if(property.value > property.base) {
            property.updated = true;
          }
          break;
      }
    },
  },
  watch: {
    showModal (value) {
      this.equipment = value
        ? this.setEquipment()
        : equipmentEmpty();
    },
  }
};
</script>

<style lang="scss">
  @import '@component/button/button.scss';
  @import './edit-equipment-modal.scss';
</style>
